@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
}

:root {
  --border: #e1e7ed;
  --background: #fff;
  --text-color: #3d4e61;
  --shadow: 0 4px 4px 0 #00000040;
  --linear-gradient: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
}

html {
  font-family: Avenir, sans-serif;
}

.search_large, .search_small {
  background: var(--background);
  border: none;
  border-radius: 4px;
  margin-top: 20px;
  display: flex;
  position: relative;
  box-shadow: 0 1px 8px #3d4e611a;
}

.large_form {
  color: var(--text-color);
  width: 100%;
  height: 63px;
  padding-left: 62px;
  font-size: 16px;
}

.small_form {
  color: var(--text-color);
  width: 100%;
  height: 36px;
  padding-left: 33px;
  font-size: 14px;
}

.large_form::placeholder {
  padding-top: 25px;
}

.small_form::placeholder {
  padding-top: 12px;
}

.search_large:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: 19px;
  width: 19px;
  height: 19px;
  position: absolute;
  bottom: 26px;
  left: 26px;
}

.search_small:after {
  content: "";
  cursor: pointer;
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: 11px;
  width: 11px;
  height: 11px;
  position: absolute;
  bottom: 16px;
  left: 13px;
}

.search_large:hover, .search_small:hover {
  box-shadow: var(--shadow);
}

.search_large:focus, .search_small:focus {
  background: var(--linear-gradient);
  outline: none;
}

